
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import {mapGetters, mapMutations} from 'vuex'
export default {
  computed: {
    ...mapGetters({
      list: 'getProductList' // 所有商品列表
    }),
    type () {
      return this.$route.params.type
    }
  },
  data () {
    return {
      height: '',
      class: '',
      sort: false,
      index: 0,
      active: 0,
      typeList: [],
      typeId: 0,
      select: false,
      bar: [{
        text: '全部',
        key: 'all'
      }, {
        text: '默认',
        key: 'default'
      }, {
        text: '最新',
        key: 'news',
        sort: false
      }, {
        text: '积分',
        key: 'score',
        sort: false
      }],
      options: [],
      option: '全部',
      // 请求的初始参数
      product: {
        type: 0,
        timeOrder: 0,
        priceOrder: 0,
        defaultOrder: 0,
        limit: 8,
        pageIndex: 0
      },
      activeList: [] // 分类商品列表
    }
  },
  mounted () {
    this.height = document.querySelector('.vux-slider').clientHeight + 'px'
  },
  components: {
    Tab,
    TabItem,
    Popup,
    Swiper,
    SwiperItem,
    Scroller,
    XImg,
    Group,
    Cell
  },
  created () {
    this.options = JSON.parse(this.$localStorage.get('goodsType'))
    this.options.push({
      id: 0,
      name: '全部'
    })
    this.product.type = this.$route.params.type
    for (const i in this.options) {
      if (this.options[i].id === this.product.type) {
        this.option = this.options[i].name
      }
    }
    this.getProduct(this)
  },
  methods: {
    handleSort () {
      this.sort = true
    },
    handleChangeSort () {
      this.sort = false
      this.getData(this.option)
    },
    handleChange (index) {
      this.active = index
      if (index === 0) {
        // 弹出选择
        console.log(0)
        this.select = true
      } else if (index === 1) {
        this.product.defaultOrder = 1
        this.getProduct(this)
      } else if (index === 2) {
        if (this.product.timeOrder === 0) {
          this.product.timeOrder = 1
          this.bar[index].sort = true
        } else {
          this.product.timeOrder = 0
          this.bar[index].sort = false
        }
        this.getProduct(this)
      } else if (index === 3) {
        if (this.product.priceOrder === 0) {
          this.product.priceOrder = 1
          this.bar[index].sort = true
        } else {
          this.product.priceOrder = 0
          this.bar[index].sort = false
        }
        this.getProduct(this)
      }
    },
    handleSelect (item) {
      console.log(item)
      this.option = item.name
      this.product.type = item.id
      this.select = false
      this.getProduct(this)
    },
    handleSwiper (index) {
    },
    ...mapMutations({
      getProduct: 'getProduct'
    })
  }
}



